import React, { useState } from 'react'
import './Portrait.scss'
// import { useMediaQuery } from "react-responsive"



const Portrait = () => {  
    const imageData=[
        "/BasicSmile.jpg",
        "/Expressionless.jpg",
        "/Surprising.jpg"
    ];
    console.log(imageData)
    const [profileImg, setProfileImg ] = useState(imageData[0]);
    const shiftImgBasicSmile = () => {setProfileImg(imageData[0])}
    const shiftImgexpressionless = () => {setProfileImg(imageData[1])}
    const shiftImgSurprising = () => {setProfileImg(imageData[2])}
    console.log(profileImg)
    // const isPortraitShrink = useMediaQuery ({
    //     query: "(min-width:768px)"
    // });
    
    // const portrait = isPortraitShrink ? "portrait" : "portraitShrink"
    return (
        <div>
            <div className="portrait" style={{backgroundImage: `url(${profileImg})`}} >
                <button onClick={() => shiftImgBasicSmile()} className="basicSmile"></button>
                <button onClick={() => shiftImgexpressionless()} className="expressionless"></button>
                <button onClick={() => shiftImgSurprising()} className="surprising"></button>            
            </div>     
        </div>
    );
    }

export default Portrait
